import Vue from 'vue'
import App from './App.vue'
import router from './router'
import VConsole from 'vconsole'
import 'vant/lib/index.css'
import {Image, Dialog, Toast, Field, Checkbox, Divider, CellGroup, Cell, RadioGroup, Radio} from 'vant'

Vue.use(Dialog).use(Toast).use(Field)
  .use(Checkbox).use(Divider).use(CellGroup)
  .use(Cell).use(RadioGroup).use(Radio).use(Image)

Vue.config.productionTip = false
Vue.prototype.$eventBus = new Vue()
localStorage.setItem('vConsole_switch_y', (window.innerHeight / 2 + ''))

// eslint-disable-next-line no-unused-vars
const console = new VConsole()

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
